'use client'

import { useEffect, useMemo, useState } from 'react'
import ShowCard from '@/components/ShowCard'

interface Show {
  id: number
  name: string
  genres: string[]
  image?: {
    medium?: string
  }
  summary?: string
}

export default function HomePage() {
  const [query, setQuery] = useState('')
  const [allShows, setAllShows] = useState<Show[]>([])
  const [selectedGenre, setSelectedGenre] = useState('')
  const [page, setPage] = useState(0)
  const [loading, setLoading] = useState(false)

  // ‚úÖ Charger les s√©ries √† chaque page
  useEffect(() => {
    const fetchShows = async () => {
      setLoading(true)
      const res = await fetch(`https://api.tvmaze.com/shows?page=${page}`)
      const data: Show[] = await res.json()
      setAllShows((prev) => [...prev, ...data.slice(0, 50)])
      setLoading(false)
    }

    fetchShows()
  }, [page])

  // üîç Filtrage local par recherche
  const filteredByQuery = useMemo(() => {
    if (!query.trim()) return allShows
    return allShows.filter((show) =>
      show.name.toLowerCase().includes(query.toLowerCase())
    )
  }, [query, allShows])

  // üéØ Filtrage par genre
  const filteredResults = useMemo(() => {
    if (!selectedGenre) return filteredByQuery
    return filteredByQuery.filter((show) =>
      show.genres.includes(selectedGenre)
    )
  }, [filteredByQuery, selectedGenre])

  // üß† Extraction des genres uniques
  const genres = useMemo(() => {
    const allGenres = allShows.flatMap((s) => s.genres)
    return Array.from(new Set(allGenres)).sort()
  }, [allShows])

  // ‚¨áÔ∏è Charger plus
  const loadMore = () => {
    setPage((prev) => prev + 1)
  }

  return (
    <main className="max-w-5xl mx-auto p-6">
      <h1 className="text-3xl font-bold mb-4">S√©ries populaires</h1>

      {/* Recherche */}
      <div className="flex gap-2 mb-6">
        <input
          type="text"
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          placeholder="Rechercher une s√©rie"
          className="flex-1 border p-2 rounded"
        />
      </div>

      {/* Filtre par cat√©gorie */}
      {genres.length > 0 && (
        <div className="mb-6">
          <label className="mr-2 font-medium">Filtrer par cat√©gorie :</label>
          <select
            value={selectedGenre}
            onChange={(e) => setSelectedGenre(e.target.value)}
            className="border rounded px-2 py-1"
          >
            <option value="">Toutes</option>
            {genres.map((genre) => (
              <option key={genre} value={genre}>
                {genre}
              </option>
            ))}
          </select>
        </div>
      )}

      {/* R√©sultats */}
      <div className="grid grid-cols-2 gap-4">
        {filteredResults.map((show) => (
          <ShowCard
            key={show.id}
            id={show.id}
            name={show.name}
            image={show.image?.medium}
          />
        ))}
      </div>

      {/* Bouton Voir plus */}
      <div className="text-center mt-8">
        <button
          onClick={loadMore}
          disabled={loading}
          className="bg-blue-600 text-white px-6 py-2 rounded hover:bg-blue-700 disabled:opacity-50"
        >
          {loading ? 'Chargement...' : 'Voir plus'}
        </button>
      </div>
    </main>
  )
}
